import React, { Component } from 'react'
import DishList from '../../components/dish-list/DishList';
import SearchBox from '../../components/search-box/SearchBox';
import Country from '../../components/country/Country';
import Header from '../../components/header/Header';
import Footer from '../../components/footer/Footer';
import './homepage.styles.css'

class Homepage extends Component {

  constructor() {
    super();

    this.state = {
      dishes: [],
      searchBox: '',
      active: 'Italian',
      dishData: ''
    }
  }

  fetchAllData = async () => {

    const lst = ['Mexican', 'Indian', 'Chinese', 'Italian', 'Japanese'];
    const url = 'https://www.themealdb.com/api/json/v1/1/filter.php?a=';
    const res = {};

    for (let elem of lst) {
      
      const link = url + elem;
      const response = await fetch(link).then(res => res.json());
      res[elem] = response.meals;

    }

    // Adding random text and prices to all objects
    for (let region of lst) {
      for (let dish of res[region]) {
        dish.price = this.generatePrice();
        dish.text = this.generateIpsumText();
      }
    }

    this.setState({ dishData: res })
    this.setState({ dishes: res[this.state.active] })

  }

  async componentDidMount() {

    await this.fetchAllData()

  }

  getRandomInt = (min, max) => {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  generatePrice = () => {
    const price = this.getRandomInt(5,15);
    return `$${price}`;
  }

  generateIpsumText = () => {

    const arr = [
        'Cupcake ipsum dolor sit',
        'Amet gummi bears halvah wafer chocolate bar bear claw marzipan marshmallow fruitcake. Liquorice ipsum macaroon bonbon',
        'Gingerbread ipsum dolor halvah chocolate lollipop bonbon brownie chocolate cake pie ',
        'Sesame snaps cake I love sweet wafer toffee pie. Cupcake ipsum dolor sit',
        'Donut ipsum dolor marzipan jelly-o marzipan jujubes jelly biscuit',
        'Topping jelly lollipop chocolate cake gingerbread sweet. Liquorice ipsum macaroon bonbon',
        'Liquorice ipsum macaroon bonbon, ipsum dolor marzipan',
        'Apple ipsum pie cookie cupcake ice cream marzipan bonbon pie.',
        'Dolor tootsie roll ipsum chocolate tiramisu croissant'
    ]

    const randomNum = this.getRandomInt(0, arr.length - 1)

    return arr[randomNum]
  }

  displayMenu = (e) => {

    const text = e.target.textContent;
    const data = this.state.dishData[text];

    this.setState({ dishes: data })
    this.setState({ active: text })

}

  handleSearch = e => {
    this.setState({ searchBox: e.target.value })
  }

  render() {

    const { dishes, searchBox } = this.state;
    const filteredDishes = dishes.filter(dish => {
      return dish.strMeal.toLowerCase().includes(searchBox.toLowerCase())
    })

    return (
    <>
        <Header />

        <div className="countryList">
          <Country name='Mexican' onclick={(e) => this.displayMenu(e)} active={this.state.active} />
          <Country name='Indian' onclick={(e) => this.displayMenu(e)} active={this.state.active} />
          <Country name='Italian' onclick={(e) => this.displayMenu(e)} active={this.state.active} />
          <Country name='Japanese' onclick={(e) => this.displayMenu(e)} active={this.state.active} />
          <Country name='Chinese' onclick={(e) => this.displayMenu(e)} active={this.state.active} />
        </div>

        <SearchBox handleSearch={this.handleSearch} />
        <DishList link={filteredDishes} />
        <Footer />
        
    </>
    )
  }
}

export default Homepage;
