import React from 'react'
import './cartItem.styles.css'

function CartItem({ link, name, price, qty, addItemQuantity, removeItemQuantity, id, removeFromCart }) {

    const itemPrice = +price.slice(1) ;
    const quantity = +qty;
    const totalPriceForOne = itemPrice * quantity;
    return (

            <tr className="item">

                <td>
                    <button className="remove-from-cart" onClick={() => removeFromCart(id)}>Remove</button>
                </td>

                <td>
                    <div className="thumb-container">
                        <div className="item-thumb" style={{backgroundImage:`url(${link})`}} />
                    </div>
                </td>

                <td className="item-name">{name}</td>

                <td>
                    <div className="item-quantity-container">
                        <button className="item-quantity-decrease" onClick={ () => removeItemQuantity(id) }>-</button>
                        <div className="item-quantity">{qty}</div>
                        <button className="item-quantity-increase" onClick={ () => addItemQuantity(id) }>+</button>
                    </div>
                </td>

                <td>{quantity} x {itemPrice} = <span className="item-price">${totalPriceForOne}</span></td>

            </tr>
    )
}

export default CartItem
