import React from 'react'
import { Link } from 'react-router-dom'
import './header.styles.css'
import { useContext } from 'react'
import { Cart } from '../../App'

function Header( ) {

    const [cart] = useContext(Cart); //redundant functions inside

    return (
        <header>
            <img src="logo.png" alt="logo" className="logo"/>
            <Link className="title" to="/Gourmet-World/">Gourmet World!</Link> 
            <Link to="/Gourmet-World/shopping-cart">
                <div className="cart-item-number"> {cart.length} </div>
                <img src="/Gourmet-World/icons/shopping-cart.svg" alt="Shopping Cart" className="shopping-cart" />
            </Link>
        </header>
    )
}

export default Header
