import React from 'react'
import Header from '../../components/header/Header'
import Footer from '../../components/footer/Footer'
import { useContext } from 'react'
import './shoppingCart.styles.css'
import { Cart } from '../../App'
import CartItem from '../../components/cart-item/CartItem'


function ShoppingCart() {

    const [cart, addItemQuantity, removeItemQuantity, removeFromCart] = useContext(Cart);
    
    const totalPrice = cart.reduce( (acc, curr) => {

        const price = +curr.price.slice(1)
        const sum = +curr.qty * price;
        return acc + sum;

    }, 0)

    return (
        
        <>
            <Header />

                <div className="cart__title">Your Cart</div>

                <table className="item-container">
                    <thead>
                        <tr>
                            <th><span></span></th>
                            <th><span>Item</span></th>
                            <th><span>Name</span></th>
                            <th><span>Quantity</span></th>
                            <th><span>Price</span></th>
                        </tr>
                    </thead>
                    <tbody>
                    { cart.map(item => (
                        <CartItem key={item.id}
                                  id={item.id} 
                                  name={item.name} 
                                  link={item.link} 
                                  price={item.price} 
                                  qty={item.qty} 
                                  addItemQuantity={addItemQuantity}
                                  removeItemQuantity={removeItemQuantity}
                                  removeFromCart={removeFromCart}/> 
                    ))}
                    </tbody>
                    <tfoot>
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td>Total:</td>
                            <td className="item-total-price">${totalPrice}</td>
                        </tr>
                    </tfoot>
                </table>
                <button className="checkout">Checkout</button>
            <Footer/>
        </>
        
    )
}

export default ShoppingCart




